import { cloudantURL } from './config'

export const search = (
  text: string,
  searchFilter: string,
  bookmark: string,
  companiesPerPage: number,
  extraQuery = '',
): Promise<any> => {
  let query = buildSearchString(normalize(text), searchFilter) + extraQuery
  let uri = encodeURIComponent(query)
  return fetch(
    `${cloudantURL}/companies/_design/companies/_search/companies?q=${uri}&limit=${companiesPerPage}`
    + (bookmark ? `&bookmark=${bookmark}` : '') + `&include_docs=true`
  )
    .then(r => r.json())
    .then(r => Promise.resolve([r.rows.map(r => r.doc), r.total_rows, r.bookmark]))
    .catch(err => {})
}

export function strToLink (str: string) {
  return removeDiacritics(str.toLocaleLowerCase().replace(/ /g, '-'))
}

export const extractId = (urlString: string) => {
  try {
    let url = new URL(urlString)
    return url.hash.substr(1)
  } catch (err) {
    return ''
  }
}

export const extractSearch = (urlString: string) => {
  try {
    let url = new URL(urlString)
    return url.search.substr(1)
  } catch (err) {
    return ''
  }
}

export function refreshHashScroll () {
  setTimeout(() => {
    let hash = window.location.hash
    if (hash.substr(1).length > 0) {
      // refresh scrolling
      window.location.href = '#'
      window.location.href = hash
    }
  }, 0)
}

export function indexesOf (c: string, str: string): number[] {
  var indexes = []
  for(var i=0; i< str.length;i++) {
    if (str[i] === c) indexes.push(i)
  }
  return indexes
}

export function changeSearchString (str) {
  var newurl = window.location.protocol
    + '//' + window.location.host + window.location.pathname + '?' + str + window.location.hash
  window.history.pushState({ path: newurl }, '', newurl)
}

export function isNumeric (n) {
  return !isNaN(parseFloat(n)) && isFinite(n)
}

export function normalize (str: string) {
  return removeDiacritics(str.toLowerCase())
}

export const buildSearchString = (textIn: string, searchFilterName?: string): string => {
  let text = textIn
    .replace(/\/|-|,/g, ' ')
    .split(' ')
    .map(p => p.trim())
    .join(' ')
  if (text === '') {
    return '*:*'
  } else {
    return text
      .split(' ')
      .filter(p => p !== '')
      .map(p => p.trim())
      .join(' ')
      .split('+')
      .map(p => p.trim())
      .join(' AND ')
      .split(' ')
      .map(
        p => p === 'AND' || p.indexOf(':') !== -1
          ? p
          : searchFilterName + ':' + p + (p.indexOf('^') === -1 ? '*' : '')
      ).join(' ')

  }
}

// remover duplicados
export function orderCompanies (empresas) {
  // se organiza en orden alfabetico
  let empresasNoDupOrd = empresas
    // .filter(em => em._id.indexOf('_design') === -1)
    .sort((a, b) => {
      if (a.name.toLowerCase() < b.name.toLowerCase()) return -1
      if (a.name.toLowerCase() > b.name.toLowerCase()) return 1
      return 0
    })

  return empresasNoDupOrd
}

